
@media screen and (max-width: 992px) {
html, body {
    background-color: #e2e4e6;
    font-size: 3vw
}
.form-control {
    height: 100%;
    font-size: 4vw
}
#label {
    font-size: 3vw
}
#item-box {
    border-radius: 15px
}
}
@media screen and (min-width: 992px) {
html, body {
    background-color: #7D7DA5;
}
#item-box {
    border-radius: 10px
}
}
html, body {
    height: 100%;
    overflow-y: hidden;
    color: #000;
    font-family: 微软雅黑细体;
}
#app {
  height: 100%
}



